<template>
<div>
  <div v-for="post in posts" :key="post.id">
    <h2>{{ post.id }} {{ post.title }}</h2>
    <p>{{ post.body }}</p>
  </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      posts:[]
    };
  },
  mounted() {
      this.$axios.get('https://jsonplaceholder.typicode.com/posts').then( (response)=> {
        //동기통신
        console.log(response) 
        this.posts = response.data;
      })
  },
};
</script>

<style scoped></style>